/* app.css */
@import url("https://fonts.googleapis.com/css?family=Press+Start+2P");
@import url("https://fonts.googleapis.com/css?family=VT323");

body {
    background-color: #ff69b4; /* Fallback background color */
    margin: 0;
    font-family: "Press Start 2P", "VT323", sans-serif; /* Apply fonts */
    color: white; /* Default text color */
}

.App {
    text-align: center;
    padding: 20px;
}

h1 {
    color: #fff; /* White color for the main heading */
    margin: 20px 0;
}

p {
    color: pink; /* Black for paragraph text */
}

button {
    background-color: #000; /* Black button */
    color: #fff; /* White text on button */
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #ff1493; /* Darker pink on hover */
}

.overlay {
    opacity: 0.85;
    width: 100%;
    height: 100%;
    z-index: 10;
    top: 0;
    left: 0;
    position: fixed;
}

.moving-background {
    z-index: -1;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.80;
    background-image: url('./assets/background-image.png'); /* Replace with your image URL */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
